  margin: 0 8px 0 8px;
  cursor: pointer; }

.header__item--chat {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 0px;
  background: url(./static/chat.svg) no-repeat;
  margin: 0 8px 0 8px;
  cursor: pointer; }

.header__item--profile {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 0px;
  background: url(./static/profile.svg) no-repeat;
  margin: 0 8px 0 8px;
  cursor: pointer; }

.header__item--repair {
  height: 40px;
  width: 40px;
  border-radius: 50%;
